.App {
  text-align: center;
  background-color: rgb(34, 34, 34, 0.5);

  position: fixed;
  z-index: 2;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
}

video {
    position: fixed;
    z-index: 1;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
}

h1 {
  font-size: 300%;
  text-decoration: underline;
  margin-bottom: 0.0em;
}

h2 {
  font-size: 200%;
  margin-top: 0.1em;
}

.countdown h2 {
  font-size: 400%;
  margin-top: 1.0em;
  margin-bottom: 0.1em;
}

h3 {
  font-size: 250%;
  margin: 0;
}

h4 {
  font-size: 250%;
  margin: 0.2em;
}

h5 {
  font-size: 250%;
  margin: 0.2em;
}



